.header {
    padding: 2rem 0;
    background-color: #E4E4E4;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 900;
}

.burgerIcon {
    cursor: pointer;
    display: none;
}

.burgerLine {
    width: 25px;
    height: 3px;
    background-color: #212121;
    margin: 6px;
}


@media only screen and (max-width: 768px) {
    .header {
        justify-content: flex-end;
        padding: 1.5rem;

    }

    .burgerIcon {
        display: block;
        z-index: 1000;
    }

    .burgerIcon.active .burgerLine:first-of-type{
        transform: rotate(-45deg) translate(-5px, 2px);
        transition: ease-in-out .3s;

    }
    .burgerIcon.active .burgerLine:nth-of-type(2){
        display: none;
    }
    .burgerIcon.active .burgerLine:last-of-type{
        transform: rotate(45deg) translate(-5px, -1px);
        transition: ease-in-out .3s;
    }
}


@media only screen and (max-width: 600px) {
    .header {
        padding: 1rem;
    }
}

